#container {
  width: 400px;
  background-color: #5f4b8bff;
  padding: 20px;
}
input {
  width: 100%;
  box-sizing: border-box;
  padding: 5px;
  -webkit-box-sizing: border-box;
  outline: none;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.list {
  margin: 15px 0;
}

.list > li {
  background-color: #e69a8dff;
  margin-bottom: 2px;
  padding: 5px;
  display: flex;
  justify-content: space-around;
}
.button {
  width: 100%;

  display: flex;
  justify-content: flex-end;
}

.btn {
  margin-top: 12px;
  padding: 10px;
  width: 85px;
  background-color: bisque;
}
